<template>
  <main>
    <aside :class="'side-' + title">
      <h2>
        <router-link :to="'/' + title">{{ pageTitle }}</router-link>
      </h2>
      <ul>
        <li
          v-for="item in list.filter((i) => i.category == title)"
          :key="item.link"
          :class="{ 'page-on': item.on }"
        >
          <router-link :to="item.link">{{ item.name }}</router-link>
        </li>
      </ul>
    </aside>
    <article>
      <Vue v-if="page === 'vue'" />
      <VueCli v-if="page === 'vue-cli'" />
      <VueBuild v-if="page === 'vue-build'" />
      <JavaScript v-if="page === 'javascript'" />
    </article>
  </main>
</template>

<script>
import { mapState } from "vuex";

import Vue from "@/post/vue/index.md";
import VueCli from "@/post/vue/cli.md";
import VueBuild from "@/post/vue/build.md";

import JavaScript from "@/post/javascript/index.md";

export default {
  components: {
    Vue,
    VueCli,
    VueBuild,
    JavaScript,
  },
  computed: {
    ...mapState(["title", "post", "list"]),
    pageTitle() {
      if (this.title == "javascript") return "JavaScript";
      else if (this.title == "react-native") return "React Native";
      else return this.title.charAt(0).toUpperCase() + this.title.slice(1);
    },
    page() {
      if (this.post != null) return `${this.title}-${this.post}`;
      else return this.title;
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_variable";
@import "@/assets/scss/_mixin";

aside {
  position: fixed;
  height: 100%;
  overflow-x: hidden;
  width: 20rem;
  border-right: $border;
  padding-bottom: 20px;
  @include responsive($responsive-size) {
    display: none;
  }
  h2 {
    text-align: center;
    margin: 30px;
    font-size: 1.4rem;
    font-weight: 400;
  }
  &.side-vue {
    h2,
    .page-on,
    a:hover {
      color: #41b883;
    }
  }
  &.side-javascript {
    h2,
    .page-on,
    a:hover {
      color: orange;
    }
  }
  &.side-python,
  &.side-react,
  &.side-react-native {
    h2,
    .page-on,
    a:hover {
      color: #3771a2;
    }
  }
  &.side-express,
  &.side-django {
    h2,
    a:hover {
      color: #2a2c2e;
    }
    .page-on {
      color: $mainColor;
    }
  }

  ul {
    margin: 30px;
    li {
      line-height: 2;
      margin-bottom: 15px;
      a {
        display: block;
      }
    }
  }
}
article {
  padding-left: 20rem;
  @include responsive($responsive-size) {
    padding: 0px;
  }
}
</style>
