<template>
  <Header />
  <router-view />
</template>

<script>
import Header from "@/components/Header";
import { mapState, mapMutations } from "vuex";

export default {
  components: { Header },
  computed: {
    ...mapState(["list"]),
  },
  methods: {
    ...mapMutations([
      "setStudy",
      "setService",
      "setMobile",
      "setTitle",
      "setPost",
      "setListOn",
    ]),
  },
  watch: {
    $route(to) {
      if (to.params.title != undefined) {
        this.setTitle(to.params.title);
        if (to.params.post != undefined) {
          this.setPost(to.params.post);
        } else {
          this.setPost(null);
        }
      }

      const index = this.list.findIndex((i) => {
        return i.link == to.path;
      });
      this.setListOn(index);

      this.setStudy(false);
      this.setService(false);
      this.setMobile(false);
    },
  },
};
</script>

