<template>
  <header>
    <h1><router-link to="/">RAYUN</router-link></h1>
    <nav>
      <font-awesome-icon :icon="['fas', 'bars']" @click="showMobile" />
      <ul class="nav__depth-1">
        <li>
          <button @click="showStudy">STUDY</button>
          <ul class="nav__depth-2" v-if="study" @mouseleave="hideStudy">
            <li v-for="study in studyList" :key="study.link">
              <router-link :to="study.link" @click="hideStudy">{{
                study.title
              }}</router-link>
            </li>
          </ul>
        </li>
        <li>
          <button @click="showService">SERVICE</button>
          <ul
            class="nav__depth-2 nav__depth-2--service"
            v-if="service"
            @mouseleave="hideService"
          >
            <li v-for="service in serviceList" :key="service.link">
              <a
                :href="service.link"
                target="_blank"
                v-html="service.title"
              ></a>
            </li>
          </ul>
        </li>
      </ul>
    </nav>
  </header>
  <ul class="nav__mobile" v-if="mobile">
    <li v-for="study in studyList" :key="study.link">
      <router-link :to="study.link" @click="hideMobile">{{
        study.title
      }}</router-link>
    </li>
    <li v-for="service in serviceList" :key="service.link">
      <a :href="service.link" target="_blank"
        >{{ service.title.replace("<br />", " ") }}</a
      >
    </li>
  </ul>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "Header",
  data() {
    return {
      studyList: [
        { link: "/javascript", title: "JavaScript" },
        { link: "/vue", title: "Vue" },
        // { link: "/react", title: "React" },
        // { link: "/react-native", title: "React Native" },
        // { link: "/express", title: "Express" },
        // { link: "/python", title: "Python" },
        // { link: "/django", title: "Django" },
      ],
      serviceList: [
        // { link: "#fridge-recipe", title: "오늘 뭐 먹지?<br />냉장고 레시피" },
        // {
        //   link: "#temperature-clothing",
        //   title: "오늘 뭐 입지?<br />기온별 옷차림",
        // },
        // { link: "#stock-trading", title: "주식 거래 시스템" },
      ],
    };
  },
  computed: {
    ...mapState(["study", "service", "mobile"]),
  },
  methods: {
    ...mapMutations(["setStudy", "setService", "setMobile"]),
    showStudy() {
      this.setService(false);
      if (this.study) this.setStudy(false);
      else this.setStudy(true);
    },
    hideStudy() {
      this.setStudy(false);
    },
    showService() {
      this.setStudy(false);
      if (this.service) this.setService(false);
      else this.setService(true);
    },
    hideService() {
      this.setService(false);
    },
    showMobile() {
      if (this.mobile) this.setMobile(false);
      else this.setMobile(true);
    },
    hideMobile() {
      this.setMobile(false);
    },
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/scss/_variable";
@import "@/assets/scss/_mixin";

header {
  width: 100%;
  background: white;
  padding: $container;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0 4px 4px -4px $shadow;
  z-index: 2;

  @include responsive($responsive-size) {
    padding: $responsive-container;
  }

  h1 {
    font-size: 2rem;

    @include responsive($responsive-size) {
      font-size: 1.5rem;
    }

    a:hover {
      color: $mainColor;
    }
  }

  .fa-bars {
    display: none;
    font-size: 1.5rem;
    @include responsive($responsive-size) {
      display: block;
      cursor: pointer;
    }
  }

  .nav__depth-1 {
    display: flex;
    font-size: 1.2rem;

    @include responsive($responsive-size) {
      display: none;
    }

    > li {
      position: relative;
      padding: 10px;

      button {
        cursor: pointer;
        &:hover {
          color: $mainColor;
        }
      }
      .nav__depth-2 {
        width: 130px;
        background: white;
        position: absolute;
        left: 5px;
        top: 60px;
        z-index: 2;
        line-height: 2;
        font-size: 1rem;
        border-bottom: 1px solid rgba(0, 0, 0, 0.2);
        border-left: 1px solid rgba(0, 0, 0, 0.2);
        border-right: 1px solid rgba(0, 0, 0, 0.2);

        li {
          a {
            display: block;
            padding: 10px;
          }
          hr {
            border: none;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
          }
          &:hover {
            color: $mainColor;
            cursor: pointer;
          }
        }
      }
      .nav__depth-2--service {
        width: 150px;
      }
    }
  }
}

.nav__mobile {
  display: none;
  @include responsive($responsive-size) {
    display: block;
    height: 100%;
    width: 100%;
    position: fixed;
    overflow-x: hidden;
    padding-top: 56px;
    background: white;
    z-index: 1;
  }

  a {
    display: block;
    padding: 20px;
    &:hover {
      color: $mainColor;
    }
  }
}
</style>
